import { NextRequest, NextResponse } from 'next/server';

/**
 * Discord WebhookÏùÑ ÌÜµÌï¥ Contact Î©îÏãúÏßÄÎ•º Ï†ÑÏÜ°ÌïòÎäî API
 * POST /api/contact
 */
export async function POST(request: NextRequest) {
    try {
        const { message } = await request.json();

        if (!message || typeof message !== 'string' || !message.trim()) {
            return NextResponse.json(
                { error: 'Î©îÏãúÏßÄÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.' },
                { status: 400 }
            );
        }

        const webhookUrl = process.env.DISCORD_WEBHOOK_URL;

        if (!webhookUrl) {
            console.error('[Contact API] DISCORD_WEBHOOK_URL ÌôòÍ≤ΩÎ≥ÄÏàòÍ∞Ä ÏÑ§Ï†ïÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.');
            return NextResponse.json(
                { error: 'ÏÑúÎ≤Ñ ÏÑ§Ï†ï Ïò§Î•ò' },
                { status: 500 }
            );
        }

        // Discord WebhookÏúºÎ°ú Î©îÏãúÏßÄ Ï†ÑÏÜ°
        const discordPayload = {
            embeds: [
                {
                    title: 'üì¨ ZED ÏõπÏÇ¨Ïù¥Ìä∏ ÏÉà Î¨∏Ïùò',
                    description: message.trim(),
                    color: 0x14b8a6, // teal-500 ÏÉâÏÉÅ
                    timestamp: new Date().toISOString(),
                    footer: {
                        text: 'ZED Contact Form',
                    },
                },
            ],
        };

        const discordResponse = await fetch(webhookUrl, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(discordPayload),
        });

        if (!discordResponse.ok) {
            const errorText = await discordResponse.text();
            console.error('[Contact API] Discord Ï†ÑÏÜ° Ïã§Ìå®:', errorText);
            return NextResponse.json(
                { error: 'Discord Ï†ÑÏÜ° Ïã§Ìå®' },
                { status: 500 }
            );
        }

        console.log('[Contact API] Î©îÏãúÏßÄ Ï†ÑÏÜ° ÏÑ±Í≥µ');
        return NextResponse.json({ success: true });

    } catch (error) {
        console.error('[Contact API] Ïò§Î•ò:', error);
        return NextResponse.json(
            { error: 'ÏÑúÎ≤Ñ Ïò§Î•ò' },
            { status: 500 }
        );
    }
}
